.clientsList {
  grid-template-columns: repeat(2, 1fr);
  display: grid
  /* width: 75%; */
}

@media (max-width: 1300px) {

.clientsList {
    grid-template-columns: repeat(2, 1fr)
}
  }

@media (max-width: 1024px) {

.clientsList {
    grid-template-columns: repeat(3, 1fr);
    width: 100%
}
  }

@media (max-width: 690px) {

.clientsList {
    grid-template-columns: repeat(2, 1fr)
}
  }

@media (max-width: 530px) {

.clientsList {
    grid-template-columns: repeat(1, 1fr)
}
  }

.client {
  /* font: var(--title40-48); */
  /* color: var(--beigePrimary); */
  font-family: "AktifoA-SemiBold";
  font-size: 92.5px;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.03;
  letter-spacing: -5.4px;
  color: #201f1e
}

@media (max-width: 1024px) {

.client {
    font: 200 28px/32px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--titleMobile28-32)
}
  }

@media (max-width: 1920px) {

.client {
    font-size: calc(40px + 40 * ((100vw - 768px) / 1152));
    line-height: calc(40px + 40 * ((100vw - 768px) / 1152))
}
  }

@media (max-width: 1460px) {

.client {
    font-size: calc(56px + 24 * ((100vw - 768px) / 672));
    line-height: calc(56px + 24 * ((100vw - 768px) / 672))
}
  }

@media (max-width: 600px) {

.client {
    font-size: calc(36px + 20 * ((100vw - 320px) / 280));
    line-height: calc(36px + 20 * ((100vw - 320px) / 280))
}
  }

@media (max-width: 425px) {

.client {
    font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--text92);
    font-size: 49px;
    font-weight: 700;
    line-height: 50px;
    letter-spacing: -2.9px
}
  }