.wrapper {
  position: relative;
  overflow: hidden;
  height: 200px
}

@media (max-width: 1024px) {

.wrapper {
    height: 420px
}
  }

@media (max-width: 768px) {

.wrapper {
    height: 260px
}
  }

@media (max-width: 425px) {

.wrapper {
    height: 10vw
}
  }

.footerImage {
  position: absolute;
  z-index: -1;
  width: auto;
  height: 480px;
  right: 0;
  bottom: -2px
}

@media (max-width: 1024px) {

.footerImage {
    height: 400px
}
  }

@media (max-width: 768px) {

.footerImage {
    height: 260px
}
  }

@media (max-width: 420px) {

.footerImage {
    height: 265px;
    width: auto;
    margin-right: 15%
}
  }

.copyright {
  display: block;
  position: absolute;
  font: 200 24px/32px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text24-32);
  color: #e0d7ce;
  color: var(--beigePrimary);
  left: calc(25% + 28px);
  bottom: 24px
}

@media (max-width: 1440px) {

.copyright {
    left: calc(25% + 17px)
}
  }

@media (max-width: 1024px) {

.copyright {
    left: calc(25% + 8px);
    bottom: 16px
}
  }

@media (max-width: 1024px) {

.copyright {
    left: 16px
}
  }

@media (max-width: 690px) {

.copyright {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24)
}
  }

@media (max-width: 600px) {

.copyright {
    display: none
}
  }

.visible {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.inVisible {
  visibility: hidden;
  opacity: 0;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  -webkit-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}