.iconLink {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 24px;
  margin-right: 32px;
}

.description {
  margin-right: 8px;
  font: 200 24px/32px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text24-32);
  color: #e0d7ce;
  color: var(--beigePrimary)
}

@media (max-width: 1200px) {

.description {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24)
}
  }

.text {
  font: 200 24px/32px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text24-32);
  font-size: 34px;
  font-weight: 300;
  line-height: 73px;
  color: #b39059
}

@media (max-width: 1200px) {

.text {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24)
}
  }

.coreappai- {
  width: 183px;
  height: 73px;
  font-family: Aeroport;
  font-size: 34px;
  font-weight: 300;
  font-style: normal;
  font-stretch: normal;
  line-height: 2.15;
  letter-spacing: -1px;
  color: #b39059;
}

.icon {
  margin-right: 8px;
  fill: #a38e7c;
}

.link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in
}

.link:hover .icon {
    fill: #ff4f05;
    fill: var(--redPrimary);
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
  }

.link:hover .text {
    color: #ff4f05;
    color: var(--redPrimary);
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
  }

.link:active .icon {
    fill: #e0421b;
    fill: var(--redSecondary);
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
  }

.link:active .text {
    color: #e0421b;
    color: var(--redSecondary);
    -webkit-transition: all 0.2s ease-in;
    transition: all 0.2s ease-in;
  }