@font-face {
  font-family: 'Aeroport-light';
  src: url('/static/fonts/Aeroport-light.woff2') format('woff2');
  font-weight: 200;
  font-display: block;
  font-style: normal;
}

@font-face {
  font-family: 'GraphikLight';
  src: url('/static/fonts/Graphik-Light-Cy-Web.woff2') format('woff2');
  font-weight: 200;
  font-display: block;
}

@font-face {
  font-family: "AktifoA-SemiBold";
  src: url("/static/fonts/AktifoA-SemiBold.ttf") format("ttf"), url("/static/fonts/AktifoA-SemiBold.otf") format("otf");
  font-weight: 200;
  font-display: block;
}

html {
  /* background-color: var(--darkPrimary); */
  /* background-color: #f2f2f2; */
  -webkit-font-smoothing: antialiased;
  max-width: 1440px;
  margin: 0 auto;
}

main {
  background-color: #f2f2f2;
  padding-top: 10px;
}

*, *::before, *::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

h1, h2, h3, p, ul, li {
  margin: 0;
  padding: 0;
}

div {
  -webkit-tap-highlight-color: transparent;
}

*, *:focus {
  outline: 0;
}

::selection {
  background-color: rgba(255, 79, 5, 0.99);
  color: #0e0f0f;
  color: var(--darkPrimary);
}

a {
  -webkit-tap-highlight-color: transparent;
  text-decoration: none;
  color: #b39059;
  color: var(--beigeSecondary);
  -webkit-transition: all 0.15s ease;
  transition: all 0.15s ease
}

a:hover {
    color: #ff4f05;
    color: var(--redPrimary);
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
  }