.wrapper {
  cursor: -webkit-grab;
  cursor: grab;
  -webkit-mask-image: -webkit-radial-gradient(circle, white, black);
  background-color: #1d1e21;
  background-color: var(--darkSecondary);
  border-radius: 6px;
  overflow: hidden;
  position: relative
}

.wrapper:hover .navButton {
    display: block !important;
  }

.wrapper:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
  }

.wrapper {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  width: 100%;
  height: 768px
}

@media (max-width: 1920px) {

.wrapper {
    height: 777px
}
  }

@media (max-width: 1440px) {

.wrapper {
    height: 52vw
}
  }

@media (max-width: 1024px) {

.wrapper {
    height: 52vw
}
  }

@media (max-width: 600px) {

.wrapper {
    height: 130vw
}
  }

.outerWrapper>div {
    width: 100%;
  }