.button {
  font: 200 20px/24px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--caption20-24);
  color: #b39059;
  color: var(--beigeSecondary);
  background-color: #1d1e21;
  background-color: var(--darkSecondary);
  -webkit-transition: all 0.12s ease-in;
  transition: all 0.12s ease-in;
  border-radius: 50px;
  padding: 2px 8px 6px 8px;
  width: 40px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  outline: none;
  border: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer
}

@media (max-width: 768px) {

.button {
    margin-left: 24px
}
  }

@media (min-width: 1025px) {
  .button:hover {
    background-color: #ff4f05;
    background-color: var(--redPrimary);
    -webkit-transition: background-color 0.12s ease-in;
    transition: background-color 0.12s ease-in;
    color: #1d1e21;
    color: var(--darkSecondary);
  }
  .button:active {
    background-color: #e0421b;
    background-color: var(--redSecondary);
    color: #1d1e21;
    color: var(--darkSecondary);
  }
}

@media (max-width: 1024px) {
  .button:active {
    background-color: #ff4f05;
    background-color: var(--redPrimary);
    color: #1d1e21;
    color: var(--darkSecondary);
    -webkit-transition: background-color 0.05s ease-in;
    transition: background-color 0.05s ease-in;
  }
}
