@media (max-width: 1024px) {

.aboutContent {
    width: 100%
}
  }

.aboutSlider {
  width: 100%;
  margin-bottom: 20px
}

@media (max-width: 1024px) {

.aboutSlider {
    margin-bottom: 40px
}
  }

@media (max-width: 425px) {

.aboutSlider {
    margin-bottom: 10px
}
  }

@media (max-width: 600px) {

.sliderDesktop {
    display: none
}
  }

@media (min-width: 601px) {

.sliderMobile {
    display: none
}
  }

.aboutTextBlocks {
  grid-template-columns: repeat(3, 1fr);
  display: grid;
  width: 100%;
  margin-top: 20px
}

@media (max-width: 1700px) {

.aboutTextBlocks {
    width: 100%
}
  }

@media (max-width: 1024px) {

.aboutTextBlocks {
    width: 100%
}
  }

@media (max-width: 690px) {

.aboutTextBlocks {
    grid-template-columns: repeat(1, 1fr)
}
  }

.TextBlockLink {
  text-decoration: none;
  color: #b39059;
  color: var(--beigeSecondary);
  -webkit-transition: all 0.15s ease;
  transition: all 0.15s ease
}

.TextBlockLink:hover {
    color: #ff4f05;
    color: var(--redPrimary);
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
  }

.TextBlockLink:active {
    color: #e0421b;
    color: var(--redSecondary);
  }

@media (max-width: 600px) {

.descriptorDesktop {
    display: none
}
  }

@media (min-width: 601px) {

.descriptorMobile {
    display: none
}
  }

.visible {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.inVisible {
  visibility: hidden;
  opacity: 0;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
  -webkit-transition: all 0.6s ease-in-out;
  transition: all 0.6s ease-in-out;
}

.placeholder {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  width: 100%;
  height: 40vw
}

@media (max-width: 1024px) {

.placeholder {
    height: 60vw
}
  }

.descriptorName {
  font: 200 33px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--title33-73);
  color: #b39059;
  color: var(--secondSizeTitle);
  margin-bottom: 0px;
  line-height: unset;
  letter-spacing: unset
}

@media (max-width: 425px) {

.descriptorName {
    font-size: 23px;
    font-weight: 300;
    line-height: 26.9px;
    letter-spacing: -0.7px;
    color: #b39059
}
  }