.iconButton {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 24px;
  margin-right: 32px;
}

.description {
  margin-right: 8px;
  font: 200 24px/32px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text24-32);
  color: #e0d7ce;
  color: var(--beigePrimary)
}

@media (max-width: 1200px) {

.description {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24)
}
  }

.text {
  font: 200 24px/32px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text24-32);
  font-size: 34px;
  font-weight: 300;
  line-height: 73px;
  color: #b39059
}

@media (max-width: 1200px) {

.text {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24)
}
  }

.disableText {
  cursor: -webkit-grab;
  cursor: grab;
  font: 200 24px/32px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text24-32);
  color: #474544
}

.disableText:active {
    cursor: -webkit-grabbing;
    cursor: grabbing;
  }

@media (max-width: 1200px) {

.disableText {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24)
}
  }

.button {
  background-color: transparent;
  padding: 0;
  border: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  -webkit-transition: all 0.15s ease-in;
  transition: all 0.15s ease-in
}

.button:hover g {
    fill: #ff4f05;
    fill: var(--redPrimary);
    -webkit-transition: all 0.15s ease-in;
    transition: all 0.15s ease-in;
  }

.button:hover rect {
    fill: #ff4f05;
    fill: var(--redPrimary);
    -webkit-transition: all 0.15s ease-in;
    transition: all 0.15s ease-in;
  }

.button:hover .text {
    color: #ff4f05;
    color: var(--redPrimary);
    -webkit-transition: all 0.15s ease-in;
    transition: all 0.15s ease-in;
  }

.button:active g {
    fill: #e0421b;
    fill: var(--redSecondary);
    -webkit-transition: all 0.15s ease-in;
    transition: all 0.15s ease-in;
  }

.button:active rect {
    fill: #e0421b;
    fill: var(--redSecondary);
    -webkit-transition: all 0.15s ease-in;
    transition: all 0.15s ease-in;
  }

.button:active .text {
    color: #e0421b;
    color: var(--redSecondary);
    -webkit-transition: all 0.15s ease-in;
    transition: all 0.15s ease-in;
  }

.icon {
  margin-right: 8px;
}