.amountIcon {
  z-index: 2;
  position: absolute;
  bottom: 12px;
  right: 16px;
}

.amount {
  position: absolute;
  color: #e0d7ce;
  opacity: 0.5;
  width: 25px;
  top: 4px;
  left: 0;
  font-size: 15px;
  line-height: 10px;
  font-family: 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font-family: var(--fontFamilyAeroport);
  font-weight: 200;
  text-align: center;
}
