.slide {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: #86878f;
}

.phone {
  position: absolute;
  overflow: hidden;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05), 0 1px 2px 0 rgba(0, 0, 0, 0.1),
    0 8px 12px 0 rgba(0, 0, 0, 0.03);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05), 0 1px 2px 0 rgba(0, 0, 0, 0.1),
    0 8px 12px 0 rgba(0, 0, 0, 0.03);
  top: 50%;
  left: 50%;
  margin-left: -122px;
  margin-top: -250px;
  width: 244px;
  height: 500px;
  border-radius: 26px;
  background-color: #1d1e21;
  background-color: var(--darkSecondary)
}

@media (max-width: 414px) {

.phone {
    -webkit-transform: scale(0.9);
            transform: scale(0.9)
}
  }

@media (max-width: 375px) {

.phone {
    -webkit-transform: scale(0.8);
            transform: scale(0.8)
}
  }

@media (max-width: 350px) {

.phone {
    -webkit-transform: scale(0.7);
            transform: scale(0.7)
}
  }

@media (max-width: 600px) {
  .video {
    top: 50%;
    left: 50%;
    margin-left: -115px;
    margin-top: -206px;
    height: 412px;
    width: 230px;
    border-radius: 3px;
  }
}
