.slide {
  white-space: pre-wrap;
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #1d1e21;
  background-color: var(--darkSecondary);
  padding-top: 96px;
  padding-left: 104px
}

@media (max-width: 1600px) {

.slide {
    padding-left: 56px;
    padding-top: 48px
}
  }

@media (max-width: 1024px) {

.slide {
    padding-left: 32px
}
  }

.content {
  width: 90%
}

@media (max-width: 1700px) {

.content {
    width: 70%
}
  }

@media (max-width: 1440px) {

.content {
    width: 80%
}
  }

@media (max-width: 1280px) {

.content {
    width: 90%
}
  }

@media (max-width: 1200px) {

.content {
    width: 70%
}
  }

@media (max-width: 768px) {

.content {
    width: 90%
}
  }

.title {
  /* font: var(--title40-48); */
  /* color: var(--beigePrimary); */
  font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text92);
  color: #e1ded8;
  font-size: 66.5px;
  line-height: 0.98;
  margin-bottom: 48px
}

@media (max-width: 1440px) {

.title {
    margin-bottom: 40px
}
  }

@media (max-width: 1200px) {

.title {
    font: 200 22px/28px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--titleMobile22-28);
    margin-bottom: 28px
}
  }