.descriptor {
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	margin-right: 44px
	/* width: calc(25% - 44px); */
}

@media (max-width: 1024px) {

.descriptor {
		width: 100%;
		margin-bottom: 24px
}
	}

@media (max-width: 425px) {

.descriptor {
		margin-bottom: 0px
}
	}

.type {
	font: 200 20px/24px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
	font: var(--caption20-24);
	color: #e0d7ce;
	color: var(--beigePrimary);
	/* margin-bottom: 24px; */
	font-family: Aeroport;
	font-size: 35px;
	font-weight: 300;
	font-style: normal;
	font-stretch: normal;
	/* line-height: 2.09; */
	letter-spacing: -1px;
	color: #b39059
}

@media (max-width: 1024px) {

.type {
		margin-bottom: 8px
}
	}

@media (max-width: 425px) {

.type {
		font-size: 23px;
		font-weight: 300;
		line-height: 26.9px;
		letter-spacing: -0.7px;
		color: #b39059
}
	}

.name {
	/* font: var(--title40-48); */
	/* color: var(--beigePrimary); */
	color: #201f1e;
	margin-bottom: 32px;
	font-family: "AktifoA-SemiBold";
	font-size: 92.5px;
	font-weight: 600;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.03;
	letter-spacing: -5.4px
}

@media (max-width: 1024px) {

.name {
		/* font: var(--titleMobile28-32);
		margin-bottom: 0; */
}
	}

@media (max-width: 1920px) {

.name {
		font-size: calc(40px + 40 * ((100vw - 768px) / 1152));
		line-height: calc(40px + 40 * ((100vw - 768px) / 1152))
}
	}

@media (max-width: 1460px) {

.name {
		font-size: calc(56px + 24 * ((100vw - 768px) / 672));
		line-height: calc(56px + 24 * ((100vw - 768px) / 672))
}
	}

@media (max-width: 600px) {

.name {
		font-size: calc(36px + 20 * ((100vw - 320px) / 280));
		line-height: calc(36px + 20 * ((100vw - 320px) / 280))
}
	}

@media (max-width: 425px) {

.name {
		font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
		font: var(--text92);
		font-size: 49px;
		font-weight: 600;
		line-height: 50px;
		letter-spacing: -2.9px;
		margin-bottom: 10px
}
	}

.clients_descriptor .name {
	font-family: Aeroport;
	font-size: 33px;
	font-weight: normal;
	font-style: normal;
	font-stretch: normal;
	line-height: 2.21;
	letter-spacing: -1px;
	color: #b39059
}

@media (max-width: 600px) {

.clients_descriptor .name {
		display: none
}
	}

.articleIconBlock {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap
}

@media (max-width: 1024px) {

.articleIconBlock {
		display: none
}
	}

.articleIconLink {
	margin-right: 8px;
	margin-bottom: 16px;
}