.slide {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: #86878f;
}

.browser {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  position: absolute;
  overflow: hidden;
  background-color: #dcdce0;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05), 0 1px 2px 0 rgba(0, 0, 0, 0.1),
    0 8px 12px 0 rgba(0, 0, 0, 0.03);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05), 0 1px 2px 0 rgba(0, 0, 0, 0.1),
    0 8px 12px 0 rgba(0, 0, 0, 0.03);
  border-radius: 5px;
  top: 50%;
  left: 50%;
  margin-left: -518px;
  margin-top: -306px;
  width: 1036px;
  height: 612px
}

@media (max-width: 1920px) {

.browser {
    margin-left: -27vw;
    margin-top: -16.5vw;
    width: 54vw;
    height: 33vw;
    border-radius: calc(4px + 3 * ((100vw - 1024px) / 896))
}
  }

@media (max-width: 1024px) {

.browser {
    margin-left: -39.5vw;
    margin-top: -24vw;
    width: 79vw;
    height: 48vw
}
  }

.header {
  position: relative;
  z-index: 1;
  height: 24px;
  width: 100%;
  background-color: #E0DEDB
}

@media (max-width: 1920px) {

.header {
    height: 1.6vw
}
  }

@media (max-width: 1024px) {

.header {
    height: 2vw
}
  }

.screen {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  overflow: hidden;
  pointer-events: none;
  width: 100%;
}

.dots {
  pointer-events: none;
  position: absolute;
  width: 32px;
  height: auto;
  top: 40%;
  left: 1.2%
}

@media (max-width: 1920px) {

.dots {
    width: calc(16px + 16 * ((100vw - 600px) / 1320))
}
  }

.background {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  pointer-events: none;
}
