.content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  padding: 0px 5px 0px 5px;
  padding: var(--cntrPadd);
  padding-top: 56px
  /* padding: 0px 56px 0px 56px; */
}

@media (max-width: 1440px) {

.content {
    /* padding: 0px 34px 0px 34px; */
}
  }

@media (max-width: 1024px) {

.content {
    /* padding: 0px 16px 0px 16px; */
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column
}
  }

.textBlocks {
  display: block
  /* width: 75%; */
}

.textBlocks>section {
    width: 33.33333%;
  }

@media (max-width: 1024px) {

.textBlocks {
    width: 100%
}
    .textBlocks>section {
      width: 100%;
    }
  }

@media (max-width: 690px) {

.textBlocks {
    display: block;
    width: auto
}
  }

.phoneLink {
  /* color: var(--beigePrimary); */
  cursor: default
}

.phoneLink:hover {
    /* color: var(--beigePrimary); */
  }

@media (max-width: 690px) {

.phoneLink {
    color: #b39059;
    color: var(--beigeSecondary);
    cursor: pointer
}
    .phoneLink:hover {
      color: #ff4f05;
      color: var(--redPrimary);
    }
  }

.descriptorName {
  font: 200 33px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--title33-73);
  color: #b39059;
  color: var(--secondSizeTitle);
  letter-spacing: -1px;
  letter-spacing: var(--title33-73LetterSpacing)
}

@media (max-width: 425px) {

.descriptorName {
    font-size: 23px;
    font-weight: 300;
    line-height: 26.9px;
    letter-spacing: -0.7px;
    color: #b39059;
    margin-bottom: 5px
}
  }

.contactsStyle {
  font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text92);
  color: #201f1e;
  color: var(--darkPrimaryText);
  line-height: 1.03;
  line-height: var(--text92LineHeight)
}

@media (max-width: 1920px) {

.contactsStyle {
    font-size: calc(40px + 40 * ((100vw - 768px) / 1152));
    line-height: calc(40px + 40 * ((100vw - 768px) / 1152))
}
  }

@media (max-width: 1460px) {

.contactsStyle {
    font-size: calc(56px + 24 * ((100vw - 768px) / 672));
    line-height: calc(56px + 24 * ((100vw - 768px) / 672))
}
  }

@media (max-width: 600px) {

.contactsStyle {
    font-size: calc(36px + 20 * ((100vw - 320px) / 280));
    line-height: calc(36px + 20 * ((100vw - 320px) / 280))
}
  }

@media (max-width: 425px) {

.contactsStyle {
    font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--text92);
    font-size: 49px;
    font-weight: 700;
    line-height: 50px;
    letter-spacing: -2.9px
}
  }

.contactsStyle a {
  color: #201f1e;
  color: var(--darkPrimaryText);
}

@media (max-width: 425px) {

.mailto {
    word-break: break-all
}
  }