.Title {
	white-space: pre-wrap;
	/* font-size: 80px; */
	/* line-height: 80px; */
	/* font-family: var(--fontFamilyAeroport); */
	/* font-family: AktifoA; */
	/* font-weight: 200; */
	/* color: var(--beigePrimary); */
	margin-top: 225px;
	color: #201f1e;
	font-family: "AktifoA-SemiBold";
	font-size: 92.5px;
	font-weight: 600;
	font-style: normal;
	font-stretch: normal;
	line-height: 1.03;
	letter-spacing: -3.4px;
	font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
	font: var(--text92);
	max-width: 50%
}
@media (max-width: 1440px) {
	.Title {
		margin-top: 15vw
}
	}
@media (max-width: 1920px) {
	.Title {
		/* font-size: calc(40px + 40 * ((100vw - 768px) / 1152));
		line-height: calc(40px + 40 * ((100vw - 768px) / 1152)); */
		font-size: calc(56px + 24 * ((100vw - 768px) / 672));
		line-height: calc(56px + 24 * ((100vw - 768px) / 672))
}
	}
@media (max-width: 1460px) {
	.Title {
		font-size: calc(56px + 24 * ((100vw - 768px) / 672));
		line-height: calc(56px + 24 * ((100vw - 768px) / 672))
}
	}
@media (max-width: 600px) {
	.Title {
		font-size: calc(36px + 20 * ((100vw - 320px) / 280));
		line-height: calc(36px + 20 * ((100vw - 320px) / 280))
		/* margin-bottom: 28px;
		margin-right: 0px; */
}
	}
@media (max-width: 425px) {
	.Title {
		line-height: 50px;
		font-size: 49px;
		color: #201f1e;
		letter-spacing: -3px;
		font-weight: 700;
		margin-top: unset;
		max-width: unset
}
	}