.cases {
	padding-top: 56px
}

@media (max-width: 600px) {

.cases {
		padding-top: 206px
}
	}

@media (max-width: 425px) {

.cases {
		padding-top: unset
}
	}

@media (max-width: 600px) {

.desktopCases {
		display: none
}
	}

@media (min-width: 601px) {

.mobileCases {
		display: none
}
	}

.plusButton {
	margin-bottom: 104px;
	margin-left: calc(25% + 28px)
}

@media (max-width: 1440px) {

.plusButton {
		margin-left: calc(25% + 17px)
}
	}

@media (max-width: 1024px) {

.plusButton {
		margin-bottom: 56px;
		margin-left: 16px
}
	}

.placeholder {
	-webkit-box-flex: 1;
	    -ms-flex-positive: 1;
	        flex-grow: 1;
	width: 100%;
	height: 40vw
}

@media (max-width: 1024px) {

.placeholder {
		height: 60vw
}
	}

.visible {
	visibility: visible;
	opacity: 1;
	-webkit-transform: translateY(0px);
	        transform: translateY(0px);
	-webkit-transition: opacity 0.6s ease-in-out, background-color 0.15s ease-in, -webkit-transform 0.6s ease-in-out;
	transition: opacity 0.6s ease-in-out, background-color 0.15s ease-in, -webkit-transform 0.6s ease-in-out;
	transition: transform 0.6s ease-in-out, opacity 0.6s ease-in-out, background-color 0.15s ease-in;
	transition: transform 0.6s ease-in-out, opacity 0.6s ease-in-out, background-color 0.15s ease-in, -webkit-transform 0.6s ease-in-out;
}

.inVisible {
	visibility: hidden;
	opacity: 0;
	-webkit-transform: translateY(20px);
	        transform: translateY(20px);
	-webkit-transition: opacity 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
	transition: opacity 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
	transition: transform 0.6s ease-in-out, opacity 0.6s ease-in-out;
	transition: transform 0.6s ease-in-out, opacity 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}