.NavLink {
  font: 200 33px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--title33-73);
  color: #b39059;
  color: var(--secondSizeTitle);
  font-size: 35px;
  margin-right: 10px;
  cursor: pointer
}
.NavLink:hover {
    color: #ff4f05;
    color: var(--redPrimary);
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
  }
.NavLink:active {
    color: #e0421b;
    color: var(--redSecondary);
  }
@media(max-width: 425px) {
  .NavLink {
    font-size: 23px;
    line-height: 26.9px;
    margin-right: 0px
}
  }