.visible {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 1.3s cubic-bezier(0.59, 0.01, 0.28, 1);
  transition: all 1.3s cubic-bezier(0.59, 0.01, 0.28, 1);
}

.inVisible {
  visibility: hidden;
  opacity: 0;
  -webkit-transform: translateY(30px);
          transform: translateY(30px);
  -webkit-transition: all 1.3s cubic-bezier(0.59, 0.01, 0.28, 1);
  transition: all 1.3s cubic-bezier(0.59, 0.01, 0.28, 1);
}

