.slide {
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  background-color: #1d1e21;
  background-color: var(--darkSecondary);
  padding-top: 48px;
  padding-left: 16px;
  padding-right: 16px
}

@media (max-width: 320px) {

.slide {
    padding-top: 32px
}
  }

.title {
  font: 200 22px/28px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--titleMobile22-28);
  color: #e0d7ce;
  color: var(--beigePrimary);
  margin-bottom: 28px
}

@media (max-width: 320px) {

.title {
    margin-bottom: 16px
}
  }

.text {
  font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--textMobile18-24);
  color: #e0d7ce;
  color: var(--beigePrimary);
  margin-bottom: 24px;
}

.articleIconBlock {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
}

.articleIconLink {
  margin-bottom: 16px;
}
