.text {
  font: 200 24px/32px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text24-32);
  font-size: 27px;
  font-weight: 300;
  line-height: 38px;
  color: #e1ded8;
  margin-bottom: 32px
}
@media (max-width: 1200px) {
  .text {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24);
    margin-bottom: 24px
}
  }