.textBlock {
  white-space: pre-wrap;
  margin-right: 32px
}

@media (max-width: 690px) {

.textBlock {
    margin-bottom: 24px
}
  }

.textBlockHeader {
  color: #b39059;
  color: var(--secondSizeTitle);
  font: 200 33px 'Aeroport-light', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--title33-73)
  /* margin-bottom: 24px; */
}

@media (max-width: 690px) {

.textBlockHeader {
    margin-bottom: 16px
}
  }

@media (max-width: 425px) {

.textBlockHeader {
    font-size: 23px;
    font-weight: 300;
    line-height: 26.9px;
    letter-spacing: -0.7px;
    color: #b39059;
    margin-bottom: 5px
}
  }

.textBlockContent {
  font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
  font: var(--text92);
  color: #201f1e;
  color: var(--darkPrimaryText);
  font-size: 49.5px;
  line-height: 1.06;
  letter-spacing: -2.5px
}

@media (max-width: 690px) {

.textBlockContent {
    font: 200 18px/24px 'GraphikLight', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--textMobile18-24)
}
  }

@media (max-width: 425px) {

.textBlockContent {
    font: 600 92.5px "AktifoA-SemiBold", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', sans-serif;
    font: var(--text92);
    font-size: 49px;
    font-weight: 700;
    line-height: 50px;
    letter-spacing: -2.9px
}
  }

.icons {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-top: 16px;
  display: none
}

.icons>img {
    margin-right: 12px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-user-drag: none;
  }