.wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  width: 100%;
  height: 100%;
  position: relative;
  padding-top: 96px;
  padding-left: 104px
}

@media (max-width: 1600px) {

.wrapper {
    padding-left: 56px;
    padding-top: 48px
}
  }

@media (max-width: 1024px) {

.wrapper {
    padding-left: 32px
}
  }

.article {
