.phone {
  right: calc(16px + 136 * ((100vw - 320px) / 1600));
  top: 96px;
  -webkit-transform-origin: right top;
          transform-origin: right top;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05), 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 8px 12px 0 rgba(0, 0, 0, 0.03);
          box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05), 0 1px 2px 0 rgba(0, 0, 0, 0.1), 0 8px 12px 0 rgba(0, 0, 0, 0.03);
  position: absolute;
  overflow: hidden;
  width: 273px;
  height: 560px;
  border-radius: 28px;
  background-color: white
}

@media (max-width: 1800px) {

.phone {
    -webkit-transform: scale(0.85);
            transform: scale(0.85)
}
  }

@media (max-width: 1600px) {

.phone {
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    top: 48px
}
  }

@media (max-width: 1300px) {

.phone {
    -webkit-transform: scale(0.75);
            transform: scale(0.75)
}
  }

@media (max-width: 1200px) {

.phone {
    -webkit-transform: scale(0.55);
            transform: scale(0.55);
    top: 56px
}
  }

@media (max-width: 1024px) {

.phone {
    -webkit-transform: scale(0.7);
            transform: scale(0.7)
}
  }

@media (max-width: 768px) {

.phone {
    -webkit-transform: scale(0.6);
            transform: scale(0.6)
}
  }

@media (max-width: 660px) {

.phone {
    -webkit-transform: scale(0.5);
            transform: scale(0.5)
}
  }

.screen {
  border: 2px solid #e1e1e2;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: absolute;
  pointer-events: none;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: top left;
     object-position: top left;
  top: 50%;
  left: 50%;
  margin-left: -128px;
  margin-top: -230px;
  height: 460px;
  width: 256px;
  border-radius: 3px;
}

